.tooltip {
  position: absolute;
  top: 1rem;
  right: 1rem;
  display: inline-block;
}

.tooltip .tooltiptext {
  visibility: hidden;
  width: 100px;
  background-color: red;
  color: white;
  font-size: 12px;
  font-weight: 900;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 2;
  right: 100%;
  margin-right: 5px;
  opacity: 0;
  transition: opacity 0.3s;
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}